<template>
  <div class="hello">
    <div v-if="isInstallMetaMask == true">
     메타마스크 설치됨
          <!-- 메타마스크 연결 버튼-->>
        <div>
          <button v-on:click="ConnectMetaMask"> 메타마스크 연결</button>
        </div>
        <div v-if="currentAddress != null">
            현재계정 :{{currentAddress}}
             <!-- 컨트랙트 연결버튼-->>
            <div v-if="conectedContrect == null"><br>
              <button v-on:click="ConnectContact"> 컨트랙트 연결</button>
            </div>
            <div v-else>
            <!-- Ownof 함수를 호출-->>
            <input  type="text" placeholder="tockenId" v-model ="ownof_tokenId" />
            <button v-on:click = "ownerOf">Ownof</button>
            <br>
            <div v-if="ownof_result != null">
              Ownof:{{ownof_result}}
            </div>
            <!-- Balanceof 함수를 호출-->
             <input  type="text" placeholder="address" v-model ="balanceof_owner" />
            <button v-on:click = "Balanceof">Balanceof</button>
           <br>
            <div v-if="balanceof_result != null">
              Balanceof:{{balanceof_result}}
            </div>
            <!-- mint 함수를 호출-->
            <input  type="text" placeholder="to" v-model ="mint_to" />
            <input  type="text" placeholder="address" v-model ="mint_address" />
            <input  type="text" placeholder="url" v-model ="mint_uri" />
            <button v-on:click = "mint">mint</button>
    
            <div v-if="mint_result != null">
              mint:{{mint_result}}
            </div>
            </div>

        </div>
     </div>
    <div v-else>메타마스크 설치 안됨, 설치 해주세요</div>
   </div>
</template>

<script>
import {ethers} from 'ethers';
import { TrackOpTypes } from 'vue';
import abi from '../assets/abi.json';

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return{
      isInstallMetaMask : window.ethereum != null && 
      window.ethereum.isMetaMask == true,  

      currentAddress : null,
      conectedContrect : null,
      //ownof에 필요한 변수
      ownof_tokenId : null,
      ownof_result : null,
      //balanceof 필요한 변수
      balanceof_owner: null, //owner
      balanceof_result : null,
     //balanceof 필요한 변수
      mint_to: null, //owner
      mint_address : null,
      mint_uri : null,
      mint_result : null,
    }
  },
  methods:{
    async ConnectMetaMask()
    {
      console.log("ConnectMetaMask 호출");
      var accpunt = await window.ethereum.request({ method:'eth_requestAccounts'});
      if(accpunt.length > 0)
      {
       this.currentAddress = accpunt[0];
       console.log(this.currentAddress);
      }
       else
      {
        console.log("계정이 없네요!!");
      }
    },
    async ConnectContact()
    {
      console.log("ConnecrContact 호출");
      var contractAddress ="0x1E178EE441e80bEB62efcE7Fb8FDAB7D68316Dbd";
      var provider  = new ethers.providers.Web3Provider(window.ethereum);
      var tempSigner = provider.getSigner();
      var conectedContrect = new  ethers.Contract(contractAddress, abi, tempSigner);
      console.log(conectedContrect);
      this.conectedContrect = conectedContrect;
    },
    async ownerOf()
    {
      console.log("ownerOf 호출");
      var ownerAddress =await this.conectedContrect.ownerOf(Number(this.ownof_tokenId));      
      console.log(ownerAddress);
      this.ownof_result = ownerAddress;
    },
    async Balanceof()
    {
      console.log("Balanceof 호출");
       var BalanceofAddress = await this.conectedContrect.balanceOf(this.balanceof_owner);      
      console.log(BalanceofAddress);
      this.balanceof_result = BalanceofAddress;
    },  
    async mint()
    {
      console.log("mint 호출");
      try
      {
       var transcation = await this.conectedContrect.mint(this.mint_to, Number(this.mint_address), this.mint_uri);      
      console.log(transcation);
      this.mint_result = "생성대기중";
      await transcation.wait();

      this.mint_result = transcation; 
      }
      catch(error)
      {
          console.log(error);
          this.mint_result = error;
      }
     
    }    
  }
}
</script>

